.sweet-loading{
  text-align: center;
  justify-content: center;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: 	#000000
}



.card {
  width: 90%;
  height: 80%;
  margin: auto;
  min-height: 512px;
  box-shadow: 0px 35px 70px 0px rgba(255, 163, 125, 0.65), 0px 30px 40px -10px rgba(255, 100, 124, 0.55);
  background: linear-gradient(180deg, rgba(255, 163, 125, 1) 0%, rgba(255, 99, 124, 1) 100%);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen
and (min-width : 320px)
and (max-width : 480px) {
.card {
    width: 90%;
    height: 80%;
    border-radius: 20px;
    box-shadow: 0px 35px 70px 0px rgba(255, 163, 125, 0.65), 0px 30px 40px -10px rgba(255, 100, 124, 0.55);
    box-shadow: none;
  }
}

/* Smartphones (landscape) ----------- */
@media only screen
and (min-width : 321px) {
.card {
    width: 90%;
    height: 80%;
    border-radius: 20px;
    box-shadow: 0px 35px 70px 0px rgba(255, 163, 125, 0.65), 0px 30px 40px -10px rgba(255, 100, 124, 0.55);
    box-shadow: none;
  }
}

/* Smartphones (portrait) ----------- */
@media only screen
and (max-width : 320px) {
.card {
    width: 90%;
    height: 80%;
    border-radius: 20px;
    box-shadow: 0px 35px 70px 0px rgba(255, 163, 125, 0.65), 0px 30px 40px -10px rgba(255, 100, 124, 0.55);
    box-shadow: none;
  }
}

/* iPads (portrait and landscape) ----------- */
@media only screen
and (min-width : 768px)
and (max-width : 1024px) {
.card {
    width: 90%;
    height: 80%;
    border-radius: 20px;
    box-shadow: 0px 35px 70px 0px rgba(255, 163, 125, 0.65), 0px 30px 40px -10px rgba(255, 100, 124, 0.55);
    box-shadow: none;
  }
}

/* iPads (landscape) ----------- */
